
































































	import uniPopup from "@/components/uni-popup/uni-popup.vue"
	export default {
		components: {uniPopup},
		data(){
			return {
				uniVeri: false, //找回密码
				uniPhone: true,  //身份验证
				uninewpwd:true,	//输入密码
				middle : false,  //提示弹出层
				bgCompany:"", //公司按钮的背景色
				companyColor:"", //公司按钮里面文字的颜色                            
				bgPersonal:"",
				personalColor:"",
				telephone:"",
				case: '', //判断是公司还是个人 1-公司，2-个人
				namePhone:"", //用户名或手机号
				rpDigit:"",//短信验证码
				countdown:'获取验证码', //定义替换的内容
				disabled:false, //让按钮是否可用
				timestatus:false, //时间，倒计时秒数
				passwordOne:'',
				passwordTwo:'',
				companyId:"",
				userId:""
			}
		},
		methods:{
			// 身份验证确认按钮
			retrBtn(e){ //输入用户名或手机号后的确认按钮
				if(this.namePhone == ""){
					uni.showToast({
						title: '请将信息填写完整，不允许为空',
						icon:"none"
					})
				}else {
					uni.request({
						url:'http://192.168.199.194/mobile/security/QueryAccount',
						method:'GET',
						data:{
							account:this.namePhone,
							case: this.case
						},
						success: (res) => {
							console.log(res);
							if(res.data.status==0){
								this.telephone = res.data.telephone;
								this.uniVeri = true,
								this.uniPhone = false,
								this.uninewpwd = true
								this.companyId=res.data.company;
								this.userId=res.data.user;
							}else{
								uni.showToast({
									title: '用户验证失败',
									icon:"none"
								})
							}
						}
					});
				}
			},
			//点击获取验证码按钮时触发的事件
			verification(evt){
				this.countdown = 60;  //倒计时秒数
	                this.timestatus = true; //秒数是否显示
	                this.clear = setInterval(this.countDown,1000); //调用倒计时函数，每隔一秒钟调用一次

				uni.request({
					url:'http://192.168.199.194/mobile/security/verCode', //获取验证时调用的接口
					method:'GET',
					data:{
						telephone: this.telephone
					},
					success: (res) => {
						if(res.data.status==0){
							uni.showToast({
								title:'验证码发送成功',
								icon:'none',
							})
						}else{
							uni.showToast({
								title:'获取验证码失败',
								icon:'none',
							})
						}
					}
				})
			},
			digit:function(evt){ //获取用户输入的验证码
				this.rpDigit=evt.target.value;
			},
			// 进行手机短信验证码后的确认按钮
			phoneBtn(e){
				console.log(this.rpDigit);
				if(this.rpDigit==""){
					uni.showToast({
						title:"请输入验证码",
						icon:'none',
					})
				}else{
					uni.request({
						url:'http://192.168.199.194/mobile/security/contrastCode',
						method:'POST',
						data:{
							telephone:this.namePhone,
							vercode:this.rpDigit
						},
						success: (res) => {
							console.log(res);
							if(res.data.status==0){
								this.uniVeri = true,
								this.uniPhone = true,
								this.uninewpwd = false
							}else{
								uni.showToast({
									title:"验证失败",
									icon:'none',
								})
							}
						}
					})
				}
			},
			pwdOne:function(evt){
				this.passwordOne=evt.target.value;
			},
			pwdTwo:function(evt){
				this.passwordTwo=evt.target.value;
			},
			uninewBtn(e){  //两次密码输入后点确定按钮触发的事件
				/* this.middle = true; */
				if(this.passwordOne==""||this.passwordTwo==""){
					uni.showToast({
						title:'请将信息填写完整',
						icon:'none'
					})
				}else if((!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.passwordOne))||(!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.passwordTwo))){
					uni.showToast({
						title:'密码格式不正确',
						icon:'none'
					})
				}else if(this.passwordOne!=this.passwordTwo){
					uni.showToast({
						title:'两次密码输入不一致',
						icon:'none'
					})
				}else{
					uni.request({
						url:'http://192.168.199.194/mobile/security/UpPassword',
						method:'GET',
						data:{
							password:this.passwordOne,
							case:this.case,
							dis:2,
							company:this.companyId,
							user:this.userId
						},
						success: (res) =>{
							console.log(res);
							if(res.data.status==0){
								uni.showToast({
									title:res.data.message,
									icon:'none'
								}),
								uni.navigateTo({
									url:'../user/safety'
								})
							}else{
								uni.showToast({
									title:res.data.message,
									icon:'none'
								})
							}
						}
					})
				}
			},
			//点击公司按钮触发的事件
			companyClick(e){
				this.bgCompany="#E93B3D";
				this.companyColor="#ffffff";
				this.bgPersonal="";
				this.personalColor="";
				
				this.case=e;
				console.log(this.case);
			},
			//点击个人按钮触发的事件
			personalClick(evt){
				this.bgPersonal="#E93B3D";
				this.personalColor="#ffffff";
				this.bgCompany="";
				this.companyColor="";
				
				this.case=evt;
				console.log(this.case);
			},
			nameAndPhone:function(evt){
				this.namePhone=evt.target.value;
				console.log(this.namePhone);
			},
			
			// 倒计时
           countDown(){
                var that = this;
                if(!that.countdown){    //倒计时完后,不等于当前秒数                
                    that.disabled = false; //按钮变为可用
                    that.timestatus = false; //秒数重新倒数
                    that.countdown = '重新获取'; //按钮内容变为“重新获取”
                    clearInterval(that.clear); //清除that1
                }else{
					--that.countdown; //倒数倒计时
				}
            }
		},
		
	}

